.about-block {
    padding: 80px 0 0;
    @media (min-width: 992px) {
        padding: 120px 0 40px;
    }

    &__header {
        font-family: 'Bebas Neue';
        font-size: 57px;
        font-weight: 400;
        line-height: 57px;
        color: #FFF;
        text-transform: uppercase;
        margin-bottom: 24px;
        @media (min-width: 576px) {
            font-size: 101px;
            line-height: 91px;
        }
        @media (min-width: 992px) {
            margin-bottom: 0;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 500;
        line-height: 34px;
        color: #FFF;
        margin-bottom: 12px;
        @media (min-width: 576px) {
            font-size: 32px;
            line-height: 45px;
            margin-bottom: 16px;
        }
    }

    &__description {
        p {
            font-size: 16px;
            font-weight: 400;
            line-height: 26px;
            color: #C7C7C7;
            margin-bottom: 40px;
            @media (min-width: 576px) {
                font-size: 18px;
                line-height: 27px;
                margin-bottom: 48px;
            }
        }
    }

    &__links {
        display: flex;
        align-items: center;
        gap: 16px;
        margin-bottom: 80px;
        @media (min-width: 992px) {
            margin-bottom: 120px;
        }

        a {
            text-decoration: none;
        }

        .btn {
            background: #D3E97A;
            border-radius: 100px;
            padding: 4px 6px 4px 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 12px;
            font-size: 16px;
            font-weight: 700;
            line-height: 16px;
            color: #0A0A0A;
            text-transform: uppercase;
        }
    }

    &__image {
        img {
            width: 100%;
        }
    }
}